.intro {
    background-color: aliceblue;

    display: flex;

    .left {
        flex: 0.6;


        .imgcontainer {
            width: 100%;
            height: 100%;
           
            opacity: 0.8;
            position: relative;

           
            display: flex;
            align-items: center;
            justify-content: center;
            // animation: blinker2 5s ease-in-out infinite;


            .word{ position: absolute;
            bottom: 50px;
        padding-left: 20px;}


            .mainImg {
                border-radius: 50%;
                width: 100%;
                max-width: 600px;

            }



        }






    }

    .right {
        flex: 0.4;
        display: flex;
        flex-direction: column;
        color: rgb(36, 31, 31);

        justify-content: space-around;
        align-items: center;

        .background2 {
            width: 350px;
            height: 350px;
            opacity: 0.4;
            position: relative;
            background-color: blanchedalmond;
            display: flex;
            align-items: center;
            justify-content: center;
            // background: rgb(255,235,205);
            // background: radial-gradient(circle, rgba(255,235,205,1) 35%, rgba(240,153,153,0) 47%);

            border-radius: 50%;

            animation: blinker3 5s ease-in-out infinite;

            p {
                font-weight: 600;
                font-size: 3rem;
                opacity: 0.5;
            }
        }

        h1 {
            padding: 20px 0;
        }

        h3 {
            width: 80%;
        }

        .blink_me {
            font-size: 3rem;
            font-weight: 500;
            rotate: 90deg;
            float: left;
            padding-bottom: 30%;
            opacity: 0.1;
            animation: blinker 2.5s ease-in-out infinite;
        }

        @keyframes blinker {
            50% {
                opacity: 0.2;
            }
        }

        @keyframes blinker2 {
            50% {
                opacity: 0.7;
                width: 170%;
                height: 180%;

            }

        }

        @keyframes blinker3 {
            50% {
                opacity: 0.2;
                width: 400px;
                height: 400px;
                margin: 0 0px -60px 0;


            }

        }
    }


    .word {
        font-family: 'Anton', sans-serif;
        perspective: 1000px;
    }

    .word span {
        cursor: pointer;
        display: inline-block;
        font-size: 100px;
        user-select: none;
        line-height: .8;



        img {
            width: 80px;
            height: 80px;
            object-fit: contain;

            &:hover {
                width: 120px;
                height: 120px;
            }
        }
    }

    .word span:nth-child(1) {
        animation: balance 3s ease-in-out 2;
        transform-origin: bottom left;
    }

    @keyframes balance {

        0%,
        100% {
            transform: rotate(0deg);
        }

        30%,
        60% {
            transform: rotate(-45deg);
        }
    }

    .word span:nth-child(2) {
        animation: shrinkjump 3s ease-in-out 2;
        transform-origin: bottom center;
    }

    @keyframes shrinkjump {

        0%,
        100% {}

        10%,
        35% {
            transform: scale(2, .2) translate(0, 0);
        }

        45%,
        50% {
            transform: scale(1) translate(0, -150px);
        }

        80% {
            transform: scale(1) translate(0, 0);
        }
    }

    .word span:nth-child(3) {
        animation: falling 7s ease-out 1;
        transform-origin: bottom center;
    }

    @keyframes falling {
        12% {
            transform: rotateX(240deg);
        }

        24% {
            transform: rotateX(150deg);
        }

        36% {
            transform: rotateX(200deg);
        }

        48% {
            transform: rotateX(175deg);
        }

        60%,
        85% {
            transform: rotateX(180deg);
        }

        100% {
            transform: rotateX(0deg);
        }
    }

    .word span:nth-child(4) {
        animation: rotate 1s ease-out 2;
    }

    @keyframes rotate {

        20%,
        80% {
            transform: rotateY(180deg);
        }

        100% {
            transform: rotateY(360deg);
        }
    }

    .word span:nth-child(5) {
        animation: toplong 1.5s linear;
    }

    @keyframes toplong {

        10%,
        40% {
            transform: translateY(-48vh) scaleY(1);
        }

        90% {
            transform: translateY(-48vh) scaleY(4);
        }
    }

  
      

}


@media (max-width:768px) {
    .intro {


        .left {
            flex: 0.3;
            .mainImg{display: none;}
        }

        .right {
            flex: 1;
            width: 500px;

            .text {
                padding-left: 20px;
                padding-top: 20px;
            }



            .background2 {
                display: none;
            }
        }
    }
}